@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html,
body {
  width: 100%;
  height: 100%;
  margin: 0;
}

/* menu styles */
header {
  width: 100%;
  position: relative;
  min-height: 10vh;
}

.nav {
  color: #333;
  font-size: 0.75rem;
  font-family: "jetbrains_monoregular";
  text-align: right;
  border-bottom: 1px solid #FF0081;
}

.nav-links {
  -webkit-text-decoration: 'none';
          text-decoration: 'none';
  z-index: 1000;

}

.nav-links,
ul {
  display: flex;
  list-style: none;
  justify-content: flex-end;
}

.nav-links ul,
li {
  padding: 0.625rem;
}

.nav-links ul,
li:hover {
  color: #FF0081;
  -webkit-transition: all .25s cubic-bezier(.645, .045, .355, 1) 0s;
  transition: all .25s cubic-bezier(.645, .045, .355, 1) 0s;
}

label {
  margin: 0 2.5rem 0 0.625rem;
  font-size: 1.625rem;
  line-height: 10vh;
  width: 1.875rem;
  height: 1.875rem;
  padding: 0.625rem;
  display: none;
}

#toggle {
  display: none;
}

/* tipography */
@font-face {
  font-family: 'jetbrains_monoregular';
  src: url(/monica-s-portfolio/static/media/jetbrainsmono-regular-webfont.b451ce56.woff2) format('woff2'),
    url(/monica-s-portfolio/static/media/jetbrainsmono-regular-webfont.0653f650.woff) format('woff');
  font-weight: normal;
  font-style: normal;
}


/* title / subtitle */
.customP {
  color: #FF0081;
  font-family: jetbrains_monoregular;
  font-size: 0.875rem;
}

h1 {
  font-size: 2.5rem;
  margin-top: 0.1em;
  margin-bottom: 0.1em;
  line-height: 1;
}

h2 {
  font-size: 2.5rem;
  color: #FF77BC;
  margin-top: 0.1em;
  margin-bottom: 0.1em;
  line-height: 1;
}

h3 {
  margin-top: 0.1em;
  line-height: 1;
}

h3:hover {
  color: #FF0081;
  -webkit-transition: all .25s cubic-bezier(.645, .045, .355, 1) 0s;
  transition: all .25s cubic-bezier(.645, .045, .355, 1) 0s;
}

p {
  font-family: Roboto;
}

a {
  text-decoration: none;
}

/* -- end of typography */

/* button style */
.btn {
  border: 1px solid #FF0081;
  padding: 1.5%;
  color: #FF0081;
  background-color: transparent;
  font-family: Roboto;
  text-align: center;
}

/* main div styles from all pages */
.mainDiv {
  display: flex;
  position: relative;
  width: 100%;
  box-sizing: border-box;
  padding: 2%;
  justify-content: space-around;
}

.divContent {
  margin-top: 5%;
  margin-left: 10%;
  box-sizing: border-box;
  width: 100%;
}

/* end of main div */
/* Tab Name from Experience Page */
.MuiTab-wrapper {
  text-transform: capitalize;
  font-family: jetbrains_monoregular;
}

.MuiTab-wrapper:hover {
  color: #FF0081;
  text-transform: capitalize;
  font-family: jetbrains_monoregular;
  -webkit-transition: all .25s cubic-bezier(.645, .045, .355, 1) 0s;
  transition: all .25s cubic-bezier(.645, .045, .355, 1) 0s;
}

/* end of tabs from Experience page */

/* When select text */

::selection {
  color: white;
  background: #FF0081;
}

/* -- end of select */
.verticalText {
  -webkit-writing-mode: vertical-rl;
      -ms-writing-mode: tb-rl;
          writing-mode: vertical-rl;
  -webkit-text-orientation: sideways-right;
          text-orientation: sideways-right;

}

.dividerDiv {
  position: relative;
  height: 10em;
}

.divider {
  margin-top: 1.25rem;
}

/* ----- RESPONSIVE DESIGN ---- */
@media only screen and (max-width: 768px) {
  .mainDiv {
    display: flex;
    flex-direction: column;
  }

  .divContent,
  .divImg {
    margin-top: 5%;
    margin-left: 5%;
  }
}

@media only screen and (max-width: 480px) {

  h1,
  h2 {
    font-size: 1.75rem;
  }

  p {
    font-size: 0.8125rem;
  }

  .divContent {
    padding-right: 3%;
  }
}

/* responsive menu */
@media only screen and (max-width: 768px) {
  label {
    display: block;
    cursor: pointer;
  }

  .nav-links {
    top: 100%;
    width: 50%;
    background-color: rgb(201, 201, 201);
    text-align: center;
    z-index: 10000;
    display: none;
    position: absolute;
  }

  .nav-links,
  ul,
  li {
    flex-direction: column;
    border-bottom: 1px solid #fff;
    margin: 0;
  }

  #toggle:checked+.nav-links {
    display: block;
    margin: 0;
  }
}

@media only screen and (max-width: 480px) {
  .nav-links {
    width: 60%;
  }

  .btn {
    border: none;
    margin-top: 0.625rem;
  }

}

/* end of responsive menu */

/* Tab Name from Experience Page */
@media only screen and (max-width: 480px) {
  .MuiTab-wrapper {
    font-size: 10px;
    text-transform: capitalize;
    font-family: jetbrains_monoregular;
  }

  .MuiTab-wrapper:hover {
    color: #FF0081;
    text-transform: capitalize;
    font-family: jetbrains_monoregular;
    -webkit-transition: all .25s cubic-bezier(.645, .045, .355, 1) 0s;
    transition: all .25s cubic-bezier(.645, .045, .355, 1) 0s;
  }

  .MuiTypography-body1 {
    width: 80%;
  }

  .MuiBox-root .MuiBox-root-227,
  .customP {
    font-size: 12px;
  }

  .MuiTypography-body1 h3 {
    line-height: 1.2;
  }
}

/* end of tabs from Experience page */
/* main div styles */



.divImg {
    margin-top: 5%;
    width: 100%;
    box-sizing: border-box;
}

.homeImg {
    width: 30%;
    margin-top: 5%;
}

.paragraph {
    margin: 1.562rem 0 1.562rem 0;
    padding-right: 35%;
}

@media only screen and (max-width: 768px) {
    .homeImg {
        width: 40%;
        margin-top: 3%;
    }
}

@media only screen and (max-width: 768px) {
    .paragraph {
        padding-right: 25%;
    }
}
/* --- Works page --- */
.workDiv {
    display: flex;
    position: relative;
    width: 100%;
    box-sizing: border-box;
}

.projectDiv {
    margin-top: 1.875rem;
}

.firstColumn {
    width: 40%;
}

.secondColumn {
    box-sizing: border-box;
    text-align: right;
    margin-top: 40px;
    width: 40%;
    padding-right: 2%;
}

.projectDescription {
    background-color: gray;
    color: #fff;
    padding: 5%;
}

.projectTechnologies {
    font-family: jetbrains_monoregular;
    font-size: 11px;
}

.fingerFoodImg {
    width: 100%;
}

.projectsP {
    color: #FF0081;
    font-family: jetbrains_monoregular;
    font-size: 11px;
}

.secondProject {
    margin-top: 50px;
}

.caesarCipher {
    width: 100%;
}

@media only screen and (max-width: 480px) {
    .workDiv {
        display: flex;
        flex-direction: column;
        box-sizing: border-box;
    }

    .firstColumn {
        width: 90%;
    }

    .secondColumn {
        width: 90%;
    }

}
